/*===== Separaciones =====*/
html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  background-color: #0A0A16;
  font-family: "Poppins", sans-serif;
  font-size: 1.6rem;
  line-height: 1.8;
  background-position: center center;
  background-repeat: no-repeat;
  background-image: url(../img/bgTelXr.png);
}
@media (min-width: 480px) {
  body {
    background-image: url(../img/bgTabletXr.png);
  }
}
@media (min-width: 768px) {
  body {
    background-image: url(../img/bgDesktop.png);
  }
}

p {
  color: #000;
  font-size: 2rem;
}

.contenedor {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img,
picture {
  width: 100%;
  display: block;
}

h1, h2, h3 {
  font-family: "Poppins", sans-serif;
  margin: 2.5rem;
  font-weight: 300;
  text-align: center;
}

h1 {
  font-size: 3.8rem;
}

h2 {
  font-size: 3.4rem;
}

h3 {
  font-size: 3rem;
}

h4 {
  font-size: 2.6rem;
}

body {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

footer {
  margin-top: auto;
}

.contenido-footer {
  background-color: #0A0A16;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.contenido-footer img {
  margin-left: 2rem;
  width: 2rem;
}
.contenido-footer h2 {
  font-size: 1.5rem;
}
@media (min-width: 768px) {
  .contenido-footer h2 {
    font-size: 2.5rem;
  }
}
.contenido-footer i {
  font-size: 2rem;
}
.contenido-footer .icons {
  margin-right: 2rem;
}
.contenido-footer .icons i {
  margin-right: 2rem;
}
.contenido-footer .icons i:hover {
  color: #9B5B90;
}

.barra {
  padding-top: 2rem;
  display: flex;
  flex-direction: column;
}
.barra .separador {
  color: #fff;
}
.barra img {
  margin: 0 auto;
  padding-bottom: 2rem;
  width: 20rem;
}
@media (min-width: 768px) {
  .barra {
    flex-direction: row;
    justify-content: space-between;
  }
}

.navegacion {
  padding-bottom: 2rem;
}
.navegacion span {
  display: none;
}
@media (min-width: 768px) {
  .navegacion span {
    margin: 2rem;
    visibility: visible;
    user-select: none;
    display: inline;
  }
}
@media (min-width: 768px) {
  .navegacion {
    display: flex;
    align-items: center;
    padding-bottom: 2rem;
  }
}
.navegacion a {
  color: #fff;
  text-decoration: none;
  font-size: 2.2rem;
  display: block;
  text-align: center;
}
@media (min-width: 768px) {
  .navegacion a {
    margin-right: 2rem;
  }
  .navegacion a:last-of-type {
    margin-right: 0;
  }
  .navegacion a:first-of-type {
    margin-left: 2rem;
  }
}
.navegacion a:hover {
  color: #9B5B90;
}

.btn {
  margin: 0 auto;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 220px;
  height: 60px;
  background-color: var(--light-theme);
  cursor: pointer;
  font-size: 24px;
  color: var(--color);
  transition: all 0.3s;
  position: relative;
  text-align: center;
  overflow: hidden;
  border-radius: 5px;
  box-shadow: 0 6px 30px -10px #cccccc;
}
.btn a {
  color: #fff;
}
.btn a:hover {
  color: #9B5B90;
}
.btn:hover {
  transform: translateX(5px) translateY(-7px);
}

.shop-now {
  position: relative;
}
.shop-now .santa-icon {
  position: absolute;
  width: 30px;
  height: 30px;
  top: -17px;
  right: -20px;
  transform: rotate(14deg);
}
.shop-now .santa-icon img {
  width: 100%;
  height: 100%;
}

.snowflake-grid {
  display: inline-grid;
  grid-template-columns: 1fr 1fr;
  grid-auto-rows: 50px;
  gap: 5px;
  width: 100px;
  transform: scale(0.4);
}
.snowflake-grid div {
  border-radius: 5px;
}
.snowflake-grid .snowflake-item {
  position: relative;
}

.to-left {
  position: absolute;
  top: -4px;
  left: -35px;
  animation: swing 3s infinite;
}
.to-left div {
  animation: flash 3s infinite;
}

.to-right {
  position: absolute;
  top: -25px;
  right: -35px;
  animation: swing 2.5s infinite;
}
.to-right div {
  animation: flash 2.5s infinite;
}

.border-left {
  border-left: 4px solid #e6dada;
}

.border-right {
  border-right: 4px solid #e6dada;
}

.border-bottom {
  border-bottom: 4px solid #e6dada;
}

.border-top {
  border-top: 4px solid #e6dada;
}

.sub-items {
  height: 28px;
  width: 28px;
}

.m-w-15 {
  width: 15px;
}

.m-h-15 {
  height: 15px;
}

.r-270 {
  transform: rotate(270deg);
}

.r-180 {
  transform: rotate(180deg);
}

.r-90 {
  transform: rotate(90deg);
}

.pull-down {
  position: absolute;
  bottom: 5px;
  right: 5px;
}

.pull-down-right {
  position: absolute;
  bottom: 5px;
  left: 5px;
}

.pull-right {
  position: absolute;
  right: 5px;
  top: 5px;
}

.pull-left {
  position: absolute;
  left: 5px;
  top: 5px;
}

.m-3 {
  margin: 3px;
}

@keyframes swing {
  50% {
    transform: rotateZ(10deg) scale(0.4);
  }
}
@keyframes flash {
  50% {
    border-color: #485563;
  }
}
.contenido-inicio h1, .contenido-inicio h2, .contenido-inicio p {
  color: #fff;
}
.contenido-inicio h1 {
  font-size: 5rem;
}
@media (min-width: 768px) {
  .contenido-inicio h1 {
    font-size: 7rem;
  }
}
.contenido-inicio h2 {
  font-size: 4rem;
}
@media (min-width: 768px) {
  .contenido-inicio h2 {
    font-size: 6rem;
  }
}
.contenido-inicio p {
  text-align: center;
}
.contenido-inicio img {
  margin: 0 auto;
  max-width: 30rem;
}

.servicios {
  display: flex;
  flex-direction: column;
  justify-content: center;
}
@media (min-width: 768px) {
  .servicios {
    flex-direction: row;
    text-align: center;
  }
}
.servicios .servicio img {
  width: 15rem;
}
.servicios .servicio h3 {
  color: #fff;
}

.container {
  width: 100%;
  max-width: 150rem;
  height: auto;
  margin: 0 auto;
  justify-items: center;
  display: grid;
  grid-template-columns: repeat(1, 1fr);
  gap: 3rem;
}
@media (min-width: 768px) {
  .container {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 3rem;
  }
}
@media (min-width: 1024px) {
  .container {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 3rem;
  }
}
.container .card {
  background-color: #10101B;
  width: 33rem;
  height: auto;
  padding-bottom: 3rem;
  border-radius: 0.8rem;
  box-shadow: 0 0.2rem 0.2rem rgba(0, 0, 0, 0.2);
  overflow: hidden;
  margin: 2rem;
  text-align: center;
  transition: all 0.25s;
}
.container .card:hover {
  transform: translateY(-1.5rem);
  box-shadow: 0 1.2rem 1.6rem rgba(0, 0, 0, 0.2);
}
.container .card img {
  width: 33rem;
  height: 22rem;
}
.container .card h4 {
  color: #fff;
  font-weight: 700;
}
.container .card p {
  padding: 0 1rem;
  font-size: 1.6rem;
  font-weight: 300;
}

h2, p {
  color: #fff;
}

h2 {
  text-transform: uppercase;
}

.texto-acercade {
  display: flex;
  flex-direction: column;
  align-content: center;
  background-color: #10101B;
  padding: 2rem;
  border-radius: 3rem;
  text-align: justify;
  align-items: center;
}
.texto-acercade img {
  border-radius: 2rem;
}
@media (min-width: 1024px) {
  .texto-acercade {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 2rem;
  }
}

i {
  font-size: 10rem;
  color: #fff;
}
@media (min-width: 768px) {
  i {
    font-size: 20rem;
  }
}

h1 {
  color: #fff;
}

.contacto {
  display: flex;
  flex-direction: column;
  align-content: center;
}
.contacto i {
  margin-left: 10rem;
  margin-right: 2rem;
}
.contacto .email {
  font-size: 2.7rem;
}
.contacto h3 {
  font-size: 2rem;
  margin: 0;
  color: #fff;
}
@media (min-width: 768px) {
  .contacto h3 {
    font-size: 3rem;
  }
}
.contacto h3:hover {
  color: #9B5B90;
  cursor: pointer;
}
.contacto i {
  margin: 0 auto;
}
@media (min-width: 768px) {
  .contacto {
    align-items: center;
    flex-direction: row;
  }
  .contacto i {
    margin: 0 10rem 2rem;
  }
}